@font-face {
  	font-family: "8bit_wonder";
  	src: url("../font/8bit_wonder/8-BIT WONDER.TTF");
}

body {
	margin: 0;
	padding: 0;
}

ul {
	margin: 0;
	padding: 0;
}

li {
	list-style-type: none;
}

#core {
	width: 100%;
	height: 100%;

	/*min-width: 100vw;*/
	min-height: 100vh;

	margin: 0;
	padding: 0;

	position: relative;

	color: white;
	font-family: "8bit_wonder";
}

/****************************************************
	LIST
****************************************************/
	@mixin step1( $width, $color ) {
	  box-shadow: 
	    $width 0 $color, 
	    -$width 0 $color,
	    0 (-$width) $color, 
	    0 $width $color;
	  margin: $width auto;
	}
	/*@mixin step2( $width, $color, $bgcolor ) {
	  box-shadow:
	    $width 0 $bgcolor,
	    -$width 0 $bgcolor,
	    0 (-$width) $bgcolor,
	    0 $width $bgcolor,
	    2*$width 0 $color,
	    -2*$width 0 $color,
	    0 (-2*$width) $color,
	    0 2*$width $color,
	    $width $width $color,
	    -$width $width $color,
	    $width (-$width) $color,
	    -$width (-$width) $color;
	}*/
	@mixin step2v2( $width, $color, $bgcolor ) {
	  box-shadow:
	    $width 0 $bgcolor,
	    -$width 0 $bgcolor,
	    0 (-$width) $bgcolor,
	    0 $width $bgcolor,
	    2*$width 0 $color,
	    -2*$width 0 $color,
	    0 (-2*$width) $color,
	    0 2*$width $color, 
	    0 0 0 $width $color;
	  margin: 2*$width auto;
	}
	/*@mixin step3( $width, $color, $bgcolor ) {
	  box-shadow:
	    $width 0 $bgcolor,
	    -$width 0 $bgcolor,
	    0 (-$width) $bgcolor,
	    0 $width $bgcolor,
	    2*$width 0 $bgcolor,
	    -2*$width 0 $bgcolor,
	    0 (-2*$width) $bgcolor,
	    0 2*$width $bgcolor, 
	    0 0 0 $width $bgcolor,
	    3*$width 0 $color,
	    -3*$width 0 $color, 
	    0 3*$width $color,
	    0 (-3*$width) $color, 
	    -2*$width (-$width) $color,
	    -$width (-2*$width) $color,
	    $width (-2*$width) $color,
	    2*$width (-$width) $color,
	    2*$width $width $color,
	    $width 2*$width $color, 
	    -$width 2*$width $color,
	    -2*$width $width $color;
	}*/
	@mixin step3v2( $width, $color, $bgcolor ) {
	  box-shadow:
	    $width 0 $bgcolor,
	    -$width 0 $bgcolor,
	    0 (-$width) $bgcolor,
	    0 $width $bgcolor,
	    2*$width 0 $bgcolor,
	    -2*$width 0 $bgcolor,
	    0 (-2*$width) $bgcolor,
	    0 2*$width $bgcolor, 
	    0 0 0 $width $bgcolor,
	    3*$width 0 $color,
	    -3*$width 0 $color, 
	    0 3*$width $color,
	    0 (-3*$width) $color,
	    0 (-$width) 0 $width $color,
	    0 $width 0 $width $color,
	    $width 0 0 $width $color,
	    -$width 0 0 $width $color;
	  margin: 3*$width auto;
	}
	@mixin step4( $width, $color, $bgcolor ) {
	  box-shadow:
	    $width 0 $bgcolor,
	    -$width 0 $bgcolor,
	    0 (-$width) $bgcolor,
	    0 $width $bgcolor,
	    2*$width 0 $bgcolor,
	    -2*$width 0 $bgcolor,
	    0 (-2*$width) $bgcolor,
	    0 2*$width $bgcolor, 
	    0 0 0 $width $bgcolor,
	    3*$width 0 $bgcolor,
	    -3*$width 0 $bgcolor, 
	    0 3*$width $bgcolor,
	    0 (-3*$width) $bgcolor,
	    0 (-$width) 0 $width $bgcolor,
	    0 $width 0 $width $bgcolor,
	    $width 0 0 $width $bgcolor,
	    -$width 0 0 $width $bgcolor,
	    4*$width 0 $color, 
	    -4*$width 0 $color,
	    0 (-4*$width) $color, 
	    0 4*$width $color,
	    2*$width 0 0 $width $color,
	    -2*$width 0 0 $width $color,
	    0 2*$width 0 $width $color,
	    0 (-2*$width) 0 $width $color,
	    0 0 0 2*$width $color;
	    margin: 4*$width auto;
	}
	@mixin other( $width, $color, $bgcolor ) {
	  box-shadow:
	    4*$width 0 $bgcolor, 
	    -4*$width 0 $bgcolor, 
	    0 (-4*$width) $bgcolor, 
	    0 4*$width $bgcolor,
	    $width 0 0 2*$width $bgcolor,
	    -$width 0 0 2*$width $bgcolor,
	    0 (-$width) 0 2*$width $bgcolor,
	    0 $width 0 2*$width $bgcolor,
	    5*$width 0 $color, 
	    -5*$width 0 $color,
	    0 (-5*$width) $color, 
	    0 5*$width $color,
	    0 0 0 3*$width $color,
	    0 2*$width 0 2*$width $color,
	    0 (-2*$width) 0 2*$width $color,
	    2*$width 0 0 2*$width $color,
	    (-2*$width) 0 0 2*$width $color;
	    margin: 5*$width auto;
	}
	.lvl1 {
	  @include other( 4px, orange, black );
	}
	.lvl2 {
	  @include step2v2( 4px, blue, black );
	}
	.lvl3 {
	  @include step3v2( 3px, red, black );
	}
	.lvl4 {
	  @include step4( 5px, green, black );
	}
	.other {
	  @include step1( 6px, black );
	}



	/****************************************************
		LIST
	****************************************************/
	.flex-list {
		display: flex;

		&.flex-list-row {
			flex-flow: row wrap;
		}

		&.flex-list-column {
			flex-direction: column;
		}

		.flex-list-item {}
	}

	.justify-center { justify-content: center; }

	.list-group {

		.list-group-item {
			width: 100%;
			height: auto;
			margin: 5px 0;
			padding: 15px;

			border-radius: 5px;
			box-sizing: border-box;

			background: #404247;

			&:first-child {}
		}
	}


/****************************************************
	WIDGETS
****************************************************/
	/****************************************************
		ARROWS
	****************************************************/
	$arrow-size: 10px;
	.arrow {
	  	width: 0; 
	  	height: 0; 

		&.arrow-up {
		  	border-left: $arrow-size solid transparent;
		  	border-right: $arrow-size solid transparent;
		  	
		  	border-bottom: $arrow-size solid black;
		}

		&.arrow-down {
		  	border-left: $arrow-size solid transparent;
		  	border-right: $arrow-size solid transparent;
		  	
		  	border-top: $arrow-size solid #f00;
		}

		&.arrow-right {
		  	border-top: $arrow-size solid transparent;
		  	border-bottom: $arrow-size solid transparent;
		  	
		  	border-left: $arrow-size solid green;
		}

		&.arrow-left {
		  	border-top: $arrow-size solid transparent;
		  	border-bottom: $arrow-size solid transparent; 
		  	
		  	border-right:$arrow-size solid blue; 
		}
	}


	/****************************************************
		PAGE
	****************************************************/
	$page-padding: 15px;
	#page {
		width: 100%;
		height: 100%;

		min-height: 100vh;

		padding: $page-padding;

		background: rebeccapurple;
		background: black;
		

		#page__list {
			width: 100%;
			
			flex: 1;
	    	flex-basis: auto;
	    	flex-shrink: 1;

			display: flex;
			flex-flow: column nowrap;
			justify-content: center;

			.page__item {
				width: 100%;
				height: 100%;

				min-height: calc(100vh - 30px);

				display: none;
				position: relative;

				&.active {
					display: block;
				}

				.page__header {
					display: flex;
    				justify-content: space-between;

    				font-size: 20px;

					.page__title {}

					.page__close {
						cursor: pointer;
					}
				}

				.page__body {
					margin: auto;
					/*max-width: 700px;*/
					letter-spacing: 3px;
				}
			}
		}
	}

	/****************************************************
		TITLE
	****************************************************/
	#titles {
		width: 100%;
		
		color: white;
		text-align: center;
		text-transform: uppercase;

		top: 0;
		z-index: 1;
		position: absolute;

    	color: #fdd486;    	
    	font-size: 25px;
    	letter-spacing: 4px;

    	display: flex;
    	align-items: center;
    	flex-flow: column;


		p {
			margin: 0;
		}

		#name {}

		#title {

		}
	}


	/****************************************************
		MSG BOX
	****************************************************/
	#msg-box {
		width: 350px;
    	/*height: 75px;*/

    	font-size: 14px;

		top: 50%;
		left: 50%;
		z-index: 1;
		position: absolute;

		background: white;
	}
	#msg-box-list {
		max-width: 350px;
    	font-size: 14px;
		
		div {}
	}


	/****************************************************
		MENU
	****************************************************/
	#menu__trigger {
		top: 0;
		left: 0;
		z-index: 10;
		position: absolute;

		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;

		cursor: pointer;

		img {
			width: 100px;
		}

		span {
    		color: white;
			margin-left: 10px;
		}
	}

	#menu__glitch {
		top: -15px;
		left: -15px;
		z-index: 11;
		position: absolute;

		width: calc(100% + 30px);
		height: calc(100% + 30px);

		display: none;

		&.active {
			display: inherit;
		}
	}

	#menu {
		width: 33%;
		height: 100%;
		max-width: 300px;
		min-height: calc(100vh - 30px);

		padding: 15px;	

    	border: 1px solid black;
		border-radius: 5px;
    	outline: 5px solid black;
    	outline-offset: -10px;

		display: none;
		z-index: 12;
		position: relative;

		flex-flow: column wrap;
		
		color: black;
		/*background: #08863e;*/
		background: white;

		&.active {
			display: flex;
		}

		#menu__list { 
			width: 100%;

			.menu__item {
				height: 75px;
				text-align: center;
				font-size: 20px;
				line-height: 75px;

				display: flex;
	    		align-items: center;
	    		justify-content: center;

	    		cursor: pointer;

	    		.arrow {
	    			display: none;
	    			margin-right: 5px;
	    			border-left-color: red;
	    		}

				&:hover {
					.arrow { display: inherit; }
				}
			}
		}
	}


	/****************************************************
		TILE
	****************************************************/
	/****************************************************
		TILE (PROJECTS)
	****************************************************/
	#projects {

		.flex-list {
    		margin: auto;
    		max-width: 960px;
		}

		.tile {
			width: 300px;
    		margin: 10px;
    		position: relative;

    		border: 5px solid white;
    		border-radius: 5px;
    		
    		display: flex;
    		flex-flow: column nowrap;
    		align-items: center;
    		justify-content: center;

			.heads {}

			.tails {
				position: absolute;
				text-align: center;
				visibility: hidden;

				a {
					color: white;

					&:hover {
						text-decoration: none;
					}
				}
			}

			&:hover {
				.heads {
					visibility: hidden;
				}
				.tails {
					visibility: visible;
				}
			}
		}
	}


	/****************************************************
		SOCIALS
	****************************************************/
	.socials {
		.social {
			margin: 10px;
		}
	}

	/****************************************************
		TEXTS
	****************************************************/
	.texts {
		max-width: 700px;
		margin: 30px auto;
		text-align: center;

		p {}
	}


/****************************************************
	PAGES
****************************************************/
	/****************************************************
		BACKGROUND
	****************************************************/
	#bg {
		width: 100%;
		height: 100%;

		/*background: #fff;*/
		/*background: #d94982;*/
		/*background: darkseagreen;*/
		background: black;

		top: 0px;
		left: 0px;
		z-index: 0;
		position: absolute;
	}


	/****************************************************
		SKILLS
	****************************************************/
	#skills {

		.page__body {

			display: flex;
			flex-flow: row wrap;

			& > div {
				width: 50%;
			}

			.texts {}

			.skills-tab {

				table {
					width: 100%;
					border: none;
				}

				thead {
					color: rgba(255, 255, 255, 0.2);
				}

				tbody {
					font-size: 20px;
				}

				tr {

					td {
						cursor: crosshair;
					}

					.arrow-right {
						visibility: hidden;
						border-left-color: white;
					}

					&:hover {
						.arrow-right {
							visibility: visible;
						}
						td:nth-child(2) {
							color: #B74842;
						}
					}
				}

				span {
					&.low { color: #B7B142; }
					&.medium { color: #2D5D74; }
					&.high { color: #40397F; }
					&.web { color: #3A9435; }
					&.desktop { color: #8B3270; }
				}
			}
		}
	}


	/****************************************************
		PROJECTS
	****************************************************/
	#projects {

		.projects-list {
			width: 980px;
			margin: auto;

			li {
				margin: 20px 0;
				border: 1px solid;

				.head {
					display: flex;
					flex-flow: row wrap;
					align-items: center;
					justify-content: space-between;

					padding: 10px;

					font-size: 20px;

					img {
						height: 150px;
					}
					i {
						font-size: 30px;
						&.fa-toggle-on { color: #3A9435; }
						&.fa-toggle-off { color: #B74842; }
					}

				}

				.body {
					padding: 0 10em;
					padding-bottom: 20px;

					display: none;
				}

				p {
					margin: 0;
				}
			}
		}

		.toggle {			

			.contener {
				width: 44px;
				height: 24px;
				padding: 2px;

				/* border: 1px solid black; */
				outline: 1px solid;
				outline-offset: 2px;
				overflow: hidden;
				
				background: #B74842;

				& > div {
					width: 50%;
					height: 100%;
					
					float: left;
					display: inline-block;
					
					font-size: 12px;
					text-align: center;
				}

				.on {
					visibility: hidden;
				}
				.off {
					visibility: visible;
				}

				&.active {
					background: #3A9435;

					.on {
						visibility: visible;
					}
					.off {
						visibility: hidden;
					}
				}
			}
		}

		.tablet {
			margin: auto;
			display: table;

			.content {}

			.b1 {
				@include step2v2(20px, white, black);

				padding: 50px;
			}

			.b2 {
				@include step1(20px, white);
			}

			.screen {
				width: 300px;
				height: 300px;
			}
		}
	}

	


